@import 'reset';
@import url('https://fonts.googleapis.com/css2?family=DM+Sans:wght@400;500;700&family=Poppins:wght@400;500;700&display=swap');
/* General */
:root {
    /* Colors */
    --blackpure: #000;
    --black: #171718;
    --black-border: #26292d;
    --white: #fff;
    --yellow: #FFC815;
    --blue: #2fb3cd;
    --grar: #3d4248;
    --dark-gray: #797f86;

    /* Nav */
    --nav-bg: var(--black);
    --nav-border: var(--black-border);
    --nav-text: var(--white);

    /* Header */
    --header-bg: var(--white);
    --header-text: var(--black-border);

    /* Footer */
    --footer-bg: var(--black);
    --footer-text: var(--white);

    /* Light mode */
    --page-bg: var(--white);
    --text-color: var(--black);
    --accent: var(--yellow);
    --accent1: var(--blue);
    --title-1: var(--accent);

    --project-card-bg: var(--white);
    --project-card-text: var(--black);
    --box-shadow: 0px 5px 35px rgba(0, 0, 0, 0.25);
}
.dark {
    /* Dark mode */
    --page-bg: #252526;
    --text-color: var(--white);
    --header-text: var(--white);
    --title-1: var(--white);
    --header-bg: var(--black);
    --project-card-bg: var(--black);
    --project-card-text: var(--white);
    --box-shadow: 0px 5px 35px rgba(0, 0, 0, 0.8);

}
html {
    font-size: 14px;
}

html,
body {

    min-height: 100vh;
    font-family: 'DM Sans', sans-serif;
    letter-spacing: -0.5px;

    /* Light mode */
    background-color: var(--page-bg);
    color: var(--text-color);
}
#root {
    min-height: 100vh;
}
.App {
    min-height: 100vh;
    display: flex;
    flex-direction: column;

}
.container {
    margin: 0 auto;
    padding: 0 15px;
    max-width: 1200px;
}
.none {
    display: none !important;
}
/* General */



.main {
    display: flex;
    padding: 0;
}
.main header {
    margin-bottom: 40px;
    display: flex;
    flex-direction: column;
    align-items: baseline;
}
.main aside {
    background: #3d4248;
    flex: 0 0 25%;
    max-width: 25%;
    position: relative;
    min-height: 70vh;
}

.main section {
    flex: 0 0 75%;
    max-width: 75%;
    padding: 40px;
}



.main h2 {
    font-size: 2em;

    font-weight: 700;
    display: inline-block;
    text-transform: uppercase;
    margin: 0;
}
.main h3{
    font-size: 36px;
    font-weight: 600;
}
.main h6 {
    vertical-align: top;
    position: relative;
    text-transform: uppercase;
    display: inline-block;
    font-size: 11px;

    font-weight: 600;
}
.main::selection{
    background-color: var(--accent)
}
.main h4 {
    font-size: 26px;
    font-weight: 600;

}

.main p {
    font-size: 15px;
    line-height: 1.8;
    color: #878c9f;
}
.divider {
    margin-bottom: 15px;
    border-bottom: 1px solid #ddd;
}

.title1 {
    z-index: 1;
    margin: 0 0 8px;
    padding: 3px 10px;
    letter-spacing: 3px;
}

.title1:after {
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    position: absolute;
    content: "";
    z-index: -1;
    -webkit-transform: skew(-20deg);
    background: var(--accent);
}

.title2 {
    z-index: 1;
    border-bottom: 1px solid #ddd;
    vertical-align: top;
    padding: 0 0 10px;
    position: relative;
}

.title2:after {
    height: 8px;
    width: 50px;
    bottom: -4px;
    left: 0;
    right: 0;
    position: absolute;
    content: "";
    z-index: -1;
    transform: skew(-36deg);
    background: #FFC815;
    -webkit-transform: skew(-36deg);
}




.projects {
    display: flex;
    flex-wrap: wrap;
    gap: 5px;
}
.projects li{
    flex-grow: 1;
    flex-basis: 20%;
    min-width: 250px;
}

@media (max-width: 955px) {
    .main aside {
        display: none;
    }
    .main section{
        flex: 0 0 100%;
        max-width: 100%;
    }
}

@media (max-width: 620px) {


    /* Nav */
    .nav-row {
        justify-content: space-between;
    }

    .dark-mode-btn {
        order: 0;
    }

    /* General */
    .main {

    }

    .title-1 {
        margin-bottom: 30px;
        font-size: 40px;
    }

    .title-2 {
        margin-bottom: 10px;
        font-size: 30px;
    }

    /* Projects */
    .project__title {
        font-size: 22px;
    }

    /* ProjectCard page */
    .project-details__desc {
        margin-bottom: 20px;
        font-size: 22px;
    }

    /* Content List */
    .content-list {
        row-gap: 20px;
    }

    .content-list__item {
        font-size: 16px;
    }

    /* Footer */
    .footer {
        padding: 40px 0 30px;
    }

    .footer__wrapper {
        row-gap: 20px;
    }

    /* Social */
    .social {
        column-gap: 20px;
    }

    .social__item {
        width: 28px;
    }



}
